<template>
  <div class="iv-container">
  <!--流程步骤条-->
  <div class="step">
    <recommend-step :step="stepProcess" :steps="steps"></recommend-step>
  </div>
  <!--业务组件渲染-->
  <router-view/>
</div>
</template>

<script>
import {mapState} from 'vuex'
const RecommendStep = () => import('@/components/RecommendStep')
export default {
  name: 'CustomDisFee',
  components: {RecommendStep},
  computed: mapState({
    stepProcess: state => state.agency.stepProcess
  }),
  data () {
    return {
      steps: [
        {id: '1', title: '自定义维度切分', icon: 'iconfont icon-ziyuan1', path: '/admin/agency/config/customDisFee/one'},
        {id: '2', title: '自定义业务小类', icon: 'iconfont icon-ziyuan2', path: '/admin/agency/config/customDisFee/two'},
        {id: '3', title: '差异配费', icon: 'iconfont icon-ziyuan3', path: '/admin/agency/config/customDisFee/three'}
      ]
    }
  }
}
</script>

<style scoped>

</style>



